function ShowHide(id){
  let checkFB = id.substr(id.length-1,1);
  if(checkFB =="f"){
   
      document.getElementById(id).style.display = "none";
      id = id.substring(0,id.length-1);// "ex) 123_123f or 123_123b 에서 f,b를 뺀 나머지"
      id += "b";
      document.getElementById(id).style.display = "block";
  }
  else if(checkFB == "b"){

      document.getElementById(id).style.display = "none";
      id = id.substring(0,id.length-1);// "ex) 123_123f or 123_123b 에서 f,b를 뺀 나머지"
      id += "f";
      document.getElementById(id).style.display = "block";
  }
}

